<template>
  <CardLayout icon="home" title="Αρχική" url="/">
    <template slot="content">
      <v-card raised class="my-4">
        <v-card-text class="subheading pa-0">
          <v-carousel interval="8000" lazy class="elevation-0">
            <v-carousel-item v-for="(slide, s) in slides" :key="s" reverse-transition="scale-transition">
              <v-jumbotron height="100%" :src="`/static/carousel/carousel_${s+1}.jpg`" gradient="to top right, rgba(0,0,0,.5), rgba(0,0,0,.5)" dark>
                <v-container fill-height class="px-5">
                  <v-layout align-center>
                    <v-flex class="px-5">
                      <h3 class="display-2">{{slide.title}}</h3>
                      <span class="subheading">{{slide.sub}}</span>
                      <v-divider class="my-3"></v-divider>
                      <div class="title mb-3">{{slide.message}}</div>
                      <v-btn large target="_blank" :href="slide.href" color="light-green" v-if="slide.btn" :to="slide.to">{{slide.btn}}</v-btn>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-jumbotron>
            </v-carousel-item>
          </v-carousel>
          <v-divider></v-divider>
          <v-container fluid fill-height class="pa-0">
            <v-layout fill-height row wrap>
              <v-flex v-for="(item, i) in items" sm12 md4 :key="i" fill-height>
                <v-card tile flat hover height="210">
                  <v-card-title class="justify-center light-green">
                    <v-icon dark x-large>{{ item.icon }}</v-icon>
                  </v-card-title>
                  <v-card-text class="pa-0">
                    <div class="lime accent-4 py-2 subheading text-xs-center">{{ item.title }}</div>
                    <p class="body-2 text-xs-center py-1 px-3">{{ item.text }}</p>
                  </v-card-text>
                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card-text>
      </v-card>
    </template>
  </CardLayout>
</template>

<script>
import CardLayout from '@/components/layouts/CardLayout'
export default {
  name: 'Landing',
  components: {
    CardLayout
  },
  data () {
    return {
      dialog: false,
      icon: 'home',
      slides: [{
        title: 'iGarden Diagnostic System',
        sub: 'Βρείτε εύκολα και γρήγορα μέσα από απλές ερωτήσεις από ποια ασθένεια πάσχει το κηπευτικό σας.',
        message: 'Πάντα συμβουλευθείτε Γεωπόνο για αποτελεσματική καταπολέμηση της ασθένειας!',
        btn: 'ΔΙΑΓΝΩΣΤΙΚΟΣ ΟΔΗΓΟΣ',
        to: '/diagnosis'
      }, {
        title: 'Γεωπονικό Πανεπιστήμιο',
        sub: 'Από πτυχιακή μελέτη του φοιτητή Χρήστου Χάρη του τμήματος Βιοτεχνολογίας του Γεωπονικού Πανεπιστημίου Αθηνών.',
        btn: 'ΔΕΙΤΕ ΠΕΡΙΣΣΟΤΕΡΑ',
        href: 'https://www2.aua.gr/el'
      }, {
        title: 'Επεξεργασία Βάσης Γνώσεως',
        sub: 'Εφόσον συνδεθείτε ως διαχειριστής, έχετε τη δυνατότητα πλήρους επεξεργασίας της βάσης γνώσεως με τα οποία λειτουργεί το iGarden.',
        btn: 'ΣΥΜΒΑΛΛΕΤΕ ΚΙ ΕΣΕΙΣ',
        to: '/edit'
      }],
      items: [{
        icon: 'done',
        title: 'Πιστοποιημένο αρχείο',
        text: 'Αρχείο από έμπειρη γνώση Φυτοπαθολογίας του Γεωπονικού Πανεπιστημίου Αθηνών και άλλων φορέων.'
      }, {
        icon: 'library_books',
        title: 'Διάγνωση',
        text: 'Μπορείτε να ακολουθήστε το διαγνωστικό οδηγό για τα κηπευτικά που έχουν εισαχθεί στο αρχείο.'
      }, {
        icon: 'edit',
        title: 'Επεξεργασία',
        text: 'Εφόσον συνδεθείτε, μπορείτε να επεξεργαστείτε ολοκληρωτικά τη βάση γνώσεως.'
      }]
    }
  },
  mounted () {
  },
  methods: {
    agree: (text) => alert(text),
    disagree: text => alert(text),
    diagnose (plant) {
      this.$router.push(`/diagnosis/${plant}`)
    }
  }
}
</script>

<style>
.whitePanel{
  background-color: rgba(255,255,255,0.55);
}
.flex-title{
  background-color: rgba(0,0,0,0.75);
  cursor: pointer;
}
.bdrd{
  border-radius: 8px;
}
.itemTitle{
  font-weight: normal;
  font-size: 17px;
}
.itemText{
  text-align: justify;
}
</style>



